<template>
  <el-container>
    <el-header>
      <div class="header">
        <div class="title">企业名称：中国平安</div>
      </div>
      <div class="" >
          <el-button type="primary" class="btn" @click="addServes">服务单新增</el-button>
          <el-button type="primary" class="btn">服务单导入</el-button>
      </div>
    </el-header>
    <el-main>
      <el-table :data="servesInfoList">
        <el-table-column prop="id" label="服务单ID">
        </el-table-column>
        <el-table-column prop="userName" label="被服务人姓名">
        </el-table-column>
        <el-table-column prop="mobile" label="被服务人电话">
        </el-table-column>
        <el-table-column prop="mobile" label="关联权益人">
        </el-table-column>
        <el-table-column prop="mobile" label="所属商品">
        </el-table-column>
        <el-table-column prop="beginTime" label="生效起期">
        </el-table-column>
        <el-table-column prop="endTime" label="生效至期">
        </el-table-column>
        <el-table-column prop="status" label="生效状态">
        </el-table-column>
        <el-table-column label="操作" width='220'>
          <template slot-scope="scope">
            <el-button @click="handleClick(scope.row)" type="text" size="small">编辑</el-button>
            <el-button type="text" size="small" @click="goodsManageClick(scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="demonstration-box">
        <!-- <span class="demonstration">显示总数</span> -->
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="pageParams.size"
          layout="total, prev, pager, next"
          :total="pageParams.totalCount"
        ></el-pagination>
      </div>
    </el-main>
  </el-container>
</template>
<script>

const queryString = require('querystring')
import page from 'data/page'
import { servesInfo } from 'data/servesList'
import queryList from 'utils/queryList'
export default {
  name: 'servesList',
  mixins: [page],
  data() {
    return {
      servesInfoList: [],
      comName: '',
      value: ''
    }
  },
  created() {
    this.id = this.$route.query.id
    this.search()
  },
  methods: {
    addServes() {
      this.$router.push('addOrEditServe?type=add')
    },
    handleClick() {
      this.$router.push('addOrEditCom?type=edit&id=66')
    },
    goodsManageClick() {
      this.$router.push('../goodsManage/?comName=中国平安')
    },
    search() {
      
      const param = {
        companyid: this.id,
        page: this.currentPage - 1,
        size: 20
      }
      this.axios.get('/order?' + queryString.stringify(param)).then((res) => {
        const code = Number(res.data.code)
        if (code === 200) {
          this.servesInfoList = res.data.data.list
          this.setPage(res.data.data)
        } else {
          this.$message(this.mes.busy)
        }
      }).catch((err) => {
        console.log(err)
        this.$message(this.mes.busy)
      })
    }
  }
}
</script>
<style lang="less" scoped>
  .header {
    text-align: left;
    border-bottom: 1px solid #eee;
    margin-bottom: 10px
  }
  .com-input {
    width: 210px;
    margin-right: 20px
  }
  .btn{
    margin-left: 20px;
  }
</style>

